import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import axios from 'axios';

const SnsWrap = styled.div`
    .title{
        width:1200px;
        margin:0 auto;
        h3{
            text-align: center;
            padding-bottom:30px;
        }
        .snsmenu{
            width:370px;
            height:63px;
            margin:0 auto;
            margin-bottom:70px;
            li{
                float:left;
                padding-right:15px;
                &:last-of-type{padding-right:0;}
            }
        }
        h4{
            content:'';
            clear:both;
            float:none;
            text-align: center;
            position:relative;
            margin-bottom:50px;
            &::before{
                content:'';
                width:1200px;
                height:1px;
                background-color:#dcd7d1;
                position:absolute;
                top:50%;
                left:0;
                z-index:-1;
            }
        }
    }
    .contents{
        width:1200px;
        height:720px;
        margin:0 auto;
        padding-bottom:150px;
        .item{
            width:237px;
            height:237px;
            background:gray;
            float:left;
            margin:0 3px 3px 0;
            img{
                object-fit:cover;
                width:237px;
                height:237px;
            }
        }
    }
`;
const Sns = () => {
    const [data, setData] = React.useState([]);
    React.useEffect(() => {
        (async () => {
            try{
                const response = await axios.get('http://localhost:3001/insta');
                setData(data => response.data);
            } catch(e) {
                console.error(e);
                alert('ajax ');
            }
        })();
    }, []);
    return (
        <SnsWrap>
            <div className="title">
                <h3>
                    <img src="../assets/img/h_sns.png" alt="sns" />
                </h3>
                <ul className="snsmenu">
                    <li>
                        <Link to="#">
                            <img src="../assets/img/sns_facebook.png" alte="facebook" />
                        </Link>
                    </li>
                    <li>
                        <Link to="#">
                            <img src="../assets/img/sns_twitter.png" alte="twitter" />
                        </Link>
                    </li>
                    <li>
                        <Link to="#">
                            <img src="../assets/img/sns_blog.png" alte="blog" />
                        </Link>
                    </li>
                    <li>
                        <Link to="#">
                            <img src="../assets/img/sns_instagram.png" alte="instagram" />
                        </Link>
                    </li>
                    <li>
                        <Link to="#">
                            <img src="../assets/img/sns_youtube.png" alte="youtube" />
                        </Link>
                    </li>
                </ul>
                <h4>
                    <img src="../assets/img/tit_sns.png" alt="baskinrobbins instagram" />
                </h4>
            </div>
            
            <div className="contents">
                {data.map((v, i) => {
                    return(
                        <div className="item" key={i}>
                            <img src={v.src} alt="instagram" />
                        </div>
                    );
                })}
                
            </div>
        </SnsWrap>
    );
};

export default Sns;