import React from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Pagination} from 'swiper';
import "swiper/css";
import "swiper/css/pagination";
import styled from 'styled-components';
import axios from 'axios';
import {Link} from 'react-router-dom';

const BannerWrap = styled.div`
    padding:100px 151px 30px;
    h3{
        text-align: center;
        height:14px;
        margin-bottom:50px;
    }
    .swiper-slide{height:500px;}
    .text{
        width:100%;
        padding:20px 0;
        p:nth-child(2){
            width:100%;
            height:40px;
            margin:10px 0;
            font-size:15px;
            font-weight:700;
            line-height:1.3;
        }
        p:nth-child(3){
            font-size:13px;
            color:#948780;
            font-weight:400;
        }
    }
    a{
        text-decoration: none;
        color:#2f231c;
    }
    .swiper-pagination-bullet{
        width:7px;
        height:7px;
        background-color:gray;
        margin-right:20px !important;
        &:hover{
            background-color:#301d17;
            width:8px;
            height:8px;
        }
    }
    .swiper-pagination-bullet-active{
        background-color:#301d17;
        width:8px;
        height:8px;
        transition:all 0.5s;
    }
`;
const Event = () => {
    const [data, setData] = React.useState([]);
    React.useEffect(() => {
        (async () => {
            try{
                const response = await axios.get('http://localhost:3001/BREVENT');
                setData(data => response.data);
            } catch (e) {
                console.error(e);
                alert('ajax ');
            }
        })();
    }, []);
    return (
        <BannerWrap>
            <h3>
                <img src="../assets/img/h_event.png" alt="BR EVENT" />
            </h3>
            <Swiper
                initialSlide={1}
                slidesPerView={4}
                slidesPerGroup={4}
                loop={true}
                loopFillGroupWithBlank={true}
                pagination={{
                clickable: true,
                }}
                modules={[Pagination]}
                
            >
                {data.map((v, i) => {
                    return(
                        <SwiperSlide key={i}>
                            <Link to="/">
                                <div className="inner">
                                    <img src={v.src} alt={v.text} />
                                </div>
                                <div className="text">
                                    <p>
                                        <img src={v.site} alt="EVENT" />
                                    </p>
                                    <p>{v.text}</p>
                                    <p>
                                        <span>{v.event}</span>
                                    </p>
                                </div>
                            </Link>
                        </SwiperSlide>
                    );
                })}
            </Swiper>
        </BannerWrap>
    );
};

export default Event;