import React from 'react';
import styled from 'styled-components'

const FooterWrap = styled.div`
    content:'';
    float:none;
    clear:both;
    font-size:13px;
    text-align: center;
    .logo{
        margin:50px 0;
    }
    .info{
        margin-bottom:30px;
        color:#635b56;
        .box1{
            width:762px;
            height:13px;
            margin:0 auto 5px;
            span{
                display:inline-block;
                padding-right:20px;
            }
        }
        .box2{
            span{
                padding-right:20px;
            }
        }
    }
    .copy{
        margin-bottom:50px;
        p{
            color:#b1adab;
            font-size:11px;
        }
    }
`;
const Copy = () => {
    return (
        <FooterWrap>
            <div className="logo">
                <img src="../assets/img/footer_logo.png" alt="" />
            </div>
            <div className='info'>
                <div className="box1">
                    <span>  : 303-81-09535</span>
                    <span>()  </span>
                    <span>   2620(11-149)</span>
                </div>
                <div className="box2">
                    <span>TEL : 080-555-3131</span>
                    <span> : </span>
                </div>
            </div>
            <div className="copy">
                <p>Copyright &copy; 2016 BRKOREA Company.All Rights Reserved.</p>
            </div>
        </FooterWrap>
    );
};

export default Copy;