import React from 'react';
import styled from 'styled-components'
import {Link} from 'react-router-dom';

const FamilyWrap = styled.div`
    width:100%;
    height:60px;
    background-color:#f9f8f7;
    ul{
        width:800px;
        height:100%;
        margin:0 auto;
        li{
            float:left;
            padding:14px 20px;
            &:nth-child(4){
                padding-left:40px;
                padding-right:5px;
            }
            &:nth-child(5){
                padding-left:5px;
                padding-right:5px;
                width:31px;
                height:31px;
                img{
                    width:100%;
                    height:100%;
                    object-fit: cover;
                }
            }
            &:nth-child(6){
                padding-left:5px;
                padding-right:60px;
            }
            a{
                width:100%;
                height:100%;
                display:block;
            }
            select{
                margin:3px 0;
                width:147px;
                padding:5px 0 5px 5px;
                border-radius:4px;
                border-color: #d1cecc;
                color:#d1cecc;
                font-size:11px;
                appearance: none;
                background:url(../assets/img/family_size_off.png) no-repeat 130px -2px;
                option{
                    color:#726454;
                }
                &.on{background-image:url(../assets/img/family_size_on.png)}
            }
        }
    }
`;
const Family = () => {
    const [close, setClick] = React.useState(false);
    const ClickSelect = React.useCallback((e) => {
        setClick(close => !close);
    }, []);
    return (
        <FamilyWrap>
            <ul>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_happypoint.png" alt="" />
                    </Link>
                </li>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_happymarket.png" alt="" />
                    </Link>
                </li>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_spc_story.png" alt="spc story" />
                    </Link>
                </li>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_norton.gif" alt="norton" />
                    </Link>
                </li>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_ccm_2.png" alt="" />
                    </Link>
                </li>
                <li>
                    <Link to="#">
                        <img src="../assets/img/btn_ksa.png" alt="ksa" />
                    </Link>
                </li>
                <li>
                    <select onClick={ClickSelect} className={close ? "on" : ""}>
                        <option value="">FAMILY SITE</option>
                        <option value="1"></option>
                        <option value="2">SPC</option>
                        <option value="3">SPC MAGAZINE</option>
                        <option value="4">BR</option>
                        <option value="5"></option>
                        <option value="6"></option>
                        <option value="7"></option>
                        <option value="8"></option>
                        <option value="9"></option>
                    </select>
                </li>
            </ul>
        </FamilyWrap>
    );
};

export default Family;