import React from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import {Pagination, Navigation, Autoplay} from 'swiper';
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import "swiper/css/autoplay";

import styled from 'styled-components';
import axios from 'axios';

const SwiperWrap = styled.div`
    .swiper-button-next{
        width:109px;
        height:109px;
        text-indent: -9999em;
        overflow: hidden;
        margin-right:30px;
        background:url(../assets/img/btn_banner_next.png) no-repeat;
    }
    .swiper-button-prev{
        width:109px;
        height:109px;
        text-indent: -9999em;
        overflow: hidden;
        margin-left:30px;
        background:url(../assets/img/btn_banner_prev.png) no-repeat;
    }
    .swiper-pagination{
        width:500px;
        left:50%;
        transform:translateX(-50%);
        margin-bottom:30px;
    }
    .swiper-pagination-bullet{
        width:10px;
        height:10px;
        background-color:white;
        opacity:1;
        margin-right:20px !important;
        &:hover{
            background-color:#301d17;
            width:13px;
            height:13px;
        }
    }
    .swiper-pagination-bullet-active{
        background-color:#301d17;
        width:13px;
        height:13px;
        transition:all .5s;
    }
`;
const Banner = () => {
    const [data, setData] = React.useState([]);
    React.useEffect(() => {
        (async () => {
            try{
                const response = await axios.get('http://localhost:3001/banner');
                setData(data => response.data);
            } catch (e) {
                console.error(e);
                alert('ajax ');
            }
        })();
    }, [])
    return (
        <SwiperWrap>
            <Swiper
                slidesPerView={1}
                loop={true}
                pagination={{
                clickable: true,
                }}
                navigation={true}
                modules={[Pagination, Navigation, Autoplay]}
                autoplay={{delay:3000}}
                initialSlide={1}
            >
                {data.map((v, i) => {
                    return(
                        <SwiperSlide key={i}>
                            <img src={v.src} alt="" />
                        </SwiperSlide>
                    );
                })}
            </Swiper>
        </SwiperWrap>
    );
};

export default Banner;