import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const MenuWrap = styled.div`
    background:url(../assets/img/bg_menu.jpg) no-repeat 50% 0;
    width:100%;
    height:1157px;
    h3{
        padding-top:100px;
        text-align: center;
    }
    .MenuInner{
        height:911px;
        background:url(../assets/img/img_menu_list_220429.png) no-repeat;
        margin:50px 270px;
        position:relative;
        
        a{
            display: block;
            text-indent: -99999em;
            overflow: hidden;
            position:absolute;
        }
        .ice{
            left:214px; 
            top:25px;
            width:397px;
            height:301px;
        }
        .cake{
            right:49px; 
            top:25px;
            width:283px;
            height:475px;
        }
        .beverage{
            left:14px;
            bottom:181px;
            width:228px;
            height:366px;
        }
        .coffe{
            left:281px;
            bottom:255px;
            width:330px;
            height:292px;
        }
        .gift{
            right:53px;
            bottom:195px;
            width:278px;
            height:182px;
        }
        .dessert{
            left:281px;
            bottom:38px;
            width:329px;
            height:180px;
        }
    }
`;
const Menu = () => {
    return (
        <MenuWrap>
            <h3>
                <img src="../assets/img/h_menu.png" alt="br menu" />
            </h3>
            <div className="MenuInner">
                <div className="menuItem">
                    <Link to="/" className="ice"></Link>
                    <Link to="/" className="cake"></Link>
                    <Link to="/" className="beverage"></Link>
                    <Link to="/" className="coffe"></Link>
                    <Link to="/" className="gift"></Link>
                    <Link to="/" className="dessert"></Link>
                </div>
            </div>
        </MenuWrap>
    );
};

export default Menu;