import React from 'react';
import {Link} from 'react-router-dom';
import styled from 'styled-components';

const InfoWrap = styled.div`
    width:100%;
    border-top: 1px solid #feaf2b;
    
    .nav{
        margin-top:30px;
        width:980px;
        margin:0 auto;
        a{
            display:inline-block;
            text-decoration:none;
            color:#726454;
            font-size:14px;
            padding:30px 20px;
            &:nth-child(5){
                font-weight:700;
                color:#ff7c98;
            }
        }
    }
`;
const Info = () => {
    return (
        <InfoWrap>
            <div className="nav">
                <Link to="#"></Link>
                <Link to="#"></Link>
                <Link to="#"></Link>
                <Link to="#"></Link>
                <Link to="#"></Link>
                <Link to="#"></Link>
                <Link to="#"></Link>
            </div>
        </InfoWrap>
    );
};

export default Info;