import React from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';

const HeaderContainer = styled.header`
    width:100%;
    height:184px;
    background:url(../img/bg_header.gif) no-repeat;
    border-top:3px solid #ff7c98;
    border-bottom:1px solid #3f291a;
    position:relative;
    .headerInner{
        width:100%;
        height:114px;
        padding-top:20px;
        position:relative;
        border-bottom:1px solid #e2d9d6;

        .sns{
            position:absolute;
            top:50px;
            left:150px;

            ul{
                display:inline-flex;
                li{
                    margin-right:10px;
                }
            }
        }
        .logo{
            margin:0 auto;
            width:92px;
            height:92px;
            a{
                width:100%;
                height:100%;
                display:inline-block;
                background:url(../assets/img/logo.png) no-repeat;
            }
        }
        .right{
            position:absolute;
            right:150px;
            top:39px;
            ul{
                display:inline-flex;
                li{
                    margin-left:10px;
                    padding-top:20px;
                    a{
                        display:inline-block;
                        text-decoration: none;
                        color:#333;
                        font-size:12px;
                    }
                }
                .search{
                    width:54px;
                    height:54px;
                    background:url(../assets/img/icon_search.png) no-repeat;
                    margin-left:20px;
                    padding-top:0;
                    a{text-indent: -99999em;width:100%;height:100%;}
                    &.on{
                        background-image:url(../assets/img/btn_search_close.gif);
                    }
                }
            }
        }
    }
    .navWrap{
        width:100%;
        height:50px;
        .inner{padding:0 150px;}
        .left{
            li{float:left; padding-right:23px;}
            .login{
                span{
                    width:35px;
                    height:50px;
                    line-height:50px;
                    text-indent: -9999em;
                    display:block;
                    background:url(../assets/img/gnb_menu.png) no-repeat;
                    background-position: 0px 0;
                }
            }
            .join{
                padding-left:23px;
                span{
                    width:27px;
                    height:50px;
                    line-height:50px;
                    text-indent: -9999em;
                    display:block;
                    background:url(../assets/img/gnb_menu.png) no-repeat;
                    background-position: -97px 0;
                }
            }
        }
        .right{
            float:right;
            li{
                float:left;
                height:50px;
                line-height:50px;
                text-align: center;
                span{
                    display:block;
                    text-indent: -99999em;
                    overflow: hidden;
                    background:url(../assets/img/gnb_menu.png) no-repeat;
                }
            }
            .flavor{
                width:223px;
                &>a{
                    display: block;
                    padding:0 28px;
                    height:100%;
                }
                span{
                    background-position:-131px 0;
                }
            }
            .menu{
                width:160px;
                &>a{
                    display: block;
                    padding:0 58px;
                    height:100%;
                }
                span{
                    background-position:-416px 0;
                }
            }
            .nutrition{
                width:160px;
                &>a{
                    display: block;
                    padding:0 28px;
                    height:100%;
                }
                span{
                    background-position:-547px 0;
                }
            }
            .event{
                width:160px;
                &>a{
                    display: block;
                    padding:0 55px;
                    height:100%;
                }
                span{
                    background-position:-734px 0;
                }
            }
            .store{
                width:167px;
                &>a{
                    display: block;
                    padding:0 60px;
                    height:100%;
                }
                span{
                    background-position:-895px 0;
                }
            }
            .about{
                width:108px;
                &>a{
                    display: block;
                    padding:0 30px;
                    height:100%;
                }
                span{
                    background-position:-1057px 0;
                }
            }
        }
    }

    /*******  */
    .subMenu{
        width:100%;
        height:0;
        background:white;
        position:absolute;
        overflow: hidden;
        opacity: 0;
        z-index:10;
        transition:all 0.3s ease;
        
        /*  */
        &.slide{
            height:314px;
            opacity:1;
            border-bottom:1px solid #483834;
        }

        .subMenuInner{
            width:100%;
            height:314px;
            .left{
                float:left;
                padding-left:150px;
                padding-top:40px;
                img{margin-top:64px;}
            }
            .right{
                float:right;
                padding-top:40px;
                padding-right:150px;
                ul{
                    float:left;
                    text-align: center;
                    li{
                        a{
                            text-decoration: none;
                            color:#7b7b7b;
                            font-weight:400;
                            &:hover{color:#ff7c98;}
                        }
                    }
                    
                    &:nth-child(2){
                        padding:0 30px;
                        li{padding:10px 0;}
                    }
                    &:nth-child(3){
                        padding:0 25px;
                        li{padding:10px 0;}
                    }
                    &:nth-child(4){
                        padding:0 50px;
                        li{padding:10px 0;}
                    }
                    &:nth-child(5){
                        padding:0 33px;
                        li{padding:10px 0;}
                    }
                    &:nth-child(6){
                        padding-left:30px;
                        li{padding:10px 0;}
                    }
                }
            }
        }
    }

    /*******  */
    .modalWrap{
        position:relative;
        height:0;
        overflow: hidden;
        transition:all 0.3s ease;
        &.open{
            height:4577px;
        }
        .modalBg{
            width:100%;
            height:4577px;
            background-color:#000;
            opacity:.5;
            position:absolute;
            left:0;
            top:0;
            z-index:15;
        }
        .modalInner{
            height:234px;
            background-color:white;
            position:absolute;
            z-index:50;
            padding:40px 150px;
            .content{
                tr{
                    th{
                        width:75px;
                        text-align: left;
                    }
                    td{
                        width:40%;
                        select{
                            width:130px;
                            height:30px;
                            border-radius:4px;
                            margin-right:15px;
                        }
                        input{
                            width:288px;
                            height:25px;
                            border:none;
                            background:#efefef;
                        }
                        .tag{
                            input{width:100%;}
                        }
                        .hash{
                            font-size:13px;
                            padding-left:10px;
                            dl{
                                dt{
                                    padding-bottom:5px;
                                    padding-top:10px;
                                    color:#9c9c9c;
                                }
                                dd{
                                    a{
                                        display:inline-block;
                                        text-decoration: none;
                                        color:#ff7c98;
                                        padding-right:11px;
                                        padding-bottom:5px;
                                    }
                                }
                            }
                        }
                    }
                    .check{
                        label{
                            margin-right:20px;
                            input{
                                width:15px;
                                height:15px;
                            }
                            &:nth-of-type(5){
                                margin-right:36px;
                            }
                            &:nth-of-type(6){
                                margin-right:5px;
                            }
                            &:nth-of-type(7){
                                margin-right:51px;
                            }
                        }
                    }
                }
            }
            button{
                width:150px;
                height:35px;
                border-radius: 35px;
                border:none;
                position:absolute;
                left:50%;
                bottom:40px;
                transform: translateX(-50%);
                background-color:#ff7c98;
                color:#fff;
            }
        }
    }
`;
const Header = () => {
    const [mouseOn, setMouseHover] = React.useState(false);
    const mouseHover = React.useCallback((e) => {
        setMouseHover(slide => true)
    }, [mouseOn]);

    const mouseLeave = React.useCallback((e) => {
        setMouseHover(slide => false)
    }, [mouseOn]);

    const [modalOn, setModalOn] = React.useState(false);
    const ClickSearch = React.useCallback((e) => {
        setModalOn(open => !open);
    }, []);

    return (
        <HeaderContainer>
            <div className="headerInner">
                <div className="sns">
                    <ul>
                        <li><Link to="/"><img src="../assets/img/icon_facebook.png" alt="" /></Link></li>
                        <li><Link to="/"><img src="../assets/img/icon_twitter.png" alt="" /></Link></li>
                        <li><Link to="/"><img src="../assets/img/icon_blog.png" alt="" /></Link></li>
                        <li><Link to="/"><img src="../assets/img/icon_instgram.png" alt="" /></Link></li>
                        <li><Link to="/"><img src="../assets/img/icon_youtube.png" alt="" /></Link></li>
                    </ul>
                </div>
                <div className="logo"><Link to="index.html"></Link></div>
                <div className="right">
                    <ul>
                        <li>
                            <Link to="/"></Link>
                        </li>
                        <li>
                            <Link to="/">CONTACTUS</Link>
                        </li>
                        <li className={"search" + " " + (modalOn ? "on" : "")} onClick={ClickSearch}>
                            <Link to="/"></Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="navWrap">
                <div className="inner">
                    <nav className="left">
                        <ul>
                            <li className="login">
                                <Link to="/">
                                    <span>LOGIN</span>
                                </Link>
                            </li>
                            <li className="join">
                                <Link to="/">
                                    <span>JOIN</span>
                                </Link>
                            </li>
                        </ul>
                    </nav>
                    <nav className="right" onMouseOver={mouseHover}>
                        <ul>
                            <li className="flavor">
                                <Link to="/">
                                    <span>FLAVOR OF THE MONTH</span>
                                </Link>
                            </li>
                            <li className="menu">
                                <Link to="/">
                                    <span>MENU</span>
                                </Link>
                            </li>
                            <li className="nutrition">
                                <Link to="/">
                                    <span>   </span>
                                </Link>
                            </li>
                            <li className="event">
                                <Link to="/">
                                    <span>EVENT</span>
                                </Link>
                            </li>
                            <li className="store">
                                <Link to="/">
                                    <span>STORE</span>
                                </Link>
                            </li>
                            <li className="about">
                                <Link to="/">
                                    <span>ABOUT</span>
                                </Link>
                            </li>
                        </ul>
                    </nav>
                </div>
            </div>
            <div className={'subMenu'+' ' + (mouseOn ? 'slide' : '')} onMouseLeave={mouseLeave}>
                <div className="subMenuInner">
                    <div className="left">
                        <img src="../assets/img/img_happypoint_app.jpg" alt="" />
                    </div>
                    <div className="right">
                        <ul>
                            <li>
                                <Link to="#">
                                    <img src="../assets/img/img_monthly_fom_220429.png" alt=" " />
                                </Link>
                            </li>
                        </ul>
                        <ul>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                        </ul>
                        <ul>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                        </ul>
                        <ul>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                        </ul>
                        <ul>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                        </ul>
                        <ul>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#"></Link>
                            </li>
                            <li>
                                <Link to="#">CONTACT US</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div className={"modalWrap"+ " " + (modalOn ? "open" : "")}>
                <div className="modalBg" onClick={ClickSearch}></div>
                <div className="modalInner">
                    <table className="content">
                        <tr>
                            <th></th>
                            <td>
                                <select>
                                    <option></option>
                                    <option></option>
                                    <option></option>
                                    <option></option>
                                    <option></option>
                                    <option></option>
                                    <option>block pack</option>
                                    <option>ready pack</option>
                                </select>
                                <input type="text" />
                            </td>
                            <th></th>
                            <td>
                                <div className="tag">
                                    <input type="text" />
                                </div>
                                <div className="hash">
                                    <dl>
                                        <dt>&#183;   </dt>
                                        <dd>
                                            <Link to="#">#</Link>
                                            <Link to="#">#</Link>
                                            <Link to="#">#</Link>
                                            <Link to="#">#</Link>
                                            <Link to="#">#</Link>
                                            <Link to="#">#</Link>
                                        </dd>
                                    </dl>
                                </div>
                            </td>
                        </tr>
                        <tr>
                            <th> </th>
                            <td className="check">
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label><br />
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label>
                                <label><input type="checkbox" /></label>
                            </td>
                        </tr>
                    </table>
                    <button></button>
                </div>
            </div>
        </HeaderContainer>
    );
};

export default Header;